"use client";

import { useCallback, useEffect, useState } from "react";
import { getReadContract, getWriteContract } from "../lib/contract";
import { useWallet } from "../context/WalletContext";
import type { Event } from "../types/event";
import type { TxState } from "../types/contract";

// ---------------------------------------------------------------------------
// useEvents â€” fetch all events from the contract
// ---------------------------------------------------------------------------

export function useEvents() {
  const [events, setEvents] = useState<Event[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchEvents = useCallback(async () => {
    setLoading(true);
    setError(null);
    try {
      const contract = getReadContract();
      if (!contract) throw new Error("Could not connect to contract");

      const count: bigint = await contract.eventCount();
      const results: Event[] = [];

      for (let i = 0; i < Number(count); i++) {
        const [name, maxTickets, soldCount, priceWei] =
          await contract.getEventInfo(i);
        results.push({
          id: i,
          name,
          description: "",
          venue: "",
          city: "",
          date: "",
          imageUrl: "",
          maxTickets: Number(maxTickets),
          soldCount: Number(soldCount),
          priceWei: priceWei.toString(),
          priceEth: "",
          category: "concert",
          artist: "",
          exists: true,
        });
      }

      setEvents(results);
    } catch (err: unknown) {
      setError(err instanceof Error ? err.message : "Failed to fetch events");
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchEvents();
  }, [fetchEvents]);

  return { events, loading, error, refetch: fetchEvents };
}

// ---------------------------------------------------------------------------
// useEvent â€” single event detail
// ---------------------------------------------------------------------------

export function useEvent(eventId: number) {
  const [event, setEvent] = useState<Event | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchEvent = async () => {
      setLoading(true);
      setError(null);
      try {
        const contract = getReadContract();
        if (!contract) throw new Error("Could not connect to contract");
        const [name, maxTickets, soldCount, priceWei] =
          await contract.getEventInfo(eventId);
        setEvent({
          id: eventId,
          name,
          description: "",
          venue: "",
          city: "",
          date: "",
          imageUrl: "",
          maxTickets: Number(maxTickets),
          soldCount: Number(soldCount),
          priceWei: priceWei.toString(),
          priceEth: "",
          category: "concert",
          artist: "",
          exists: true,
        });
      } catch (err: unknown) {
        setError(err instanceof Error ? err.message : "Failed to fetch event");
      } finally {
        setLoading(false);
      }
    };
    fetchEvent();
  }, [eventId]);

  return { event, loading, error };
}

// ---------------------------------------------------------------------------
// useMintTicket â€” purchase a ticket for an event
// ---------------------------------------------------------------------------

export function useMintTicket() {
  const { ensureCorrectChain } = useWallet();
  const [tx, setTx] = useState<TxState>({ status: "idle" });

  const mint = useCallback(
    async (eventId: number, priceWei: string) => {
      const onCorrectChain = await ensureCorrectChain();
      if (!onCorrectChain) return;

      setTx({ status: "pending" });
      try {
        const contract = await getWriteContract();
        if (!contract) throw new Error("Wallet not connected");

        const receipt = await contract.mintTicket(eventId, {
          value: BigInt(priceWei),
        });
        await receipt.wait();
        setTx({ status: "success", hash: receipt.hash });
      } catch (err: unknown) {
        setTx({
          status: "error",
          error: err instanceof Error ? err.message : "Transaction failed",
        });
      }
    },
    [ensureCorrectChain]
  );

  const reset = useCallback(() => setTx({ status: "idle" }), []);
  return { mint, tx, reset };
}
